body {
  padding: 107px 50px;
  margin: 0; }

@font-face {
  font-family: "SFUIDisplayMedium";
  src: url("../../fonts/SFUIDisplay-Medium.ttf") format("truetype");
  font-style: normal;
  font-weight: normal; }

* {
  box-sizing: border-box;
  font-family: SFUIDisplayMedium;
  font-style: normal; }

.container {
  width: 100%;
  margin: auto;
  max-width: 1788px; }
  .container__list {
    display: flex;
    justify-content: space-between; }
    @media screen and (max-width: 1600px) {
      .container__list {
        flex-wrap: wrap;
        justify-content: center; } }
  .container__item {
    background-color: #F8FAFC;
    width: calc(33.3% - 35px);
    border-radius: 25px;
    padding: 44px 30px 37px 46px;
    position: relative;
    transition: 0.3s; }
    @media screen and (max-width: 1600px) {
      .container__item {
        min-width: 450px;
        margin: 50px; } }
    .container__item:hover {
      filter: drop-shadow(0px 4px 45px #C3D4E4); }
  .container__compare {
    position: absolute;
    top: 44px;
    right: 29px;
    width: 46px;
    height: 46px;
    background: #FFFFFF;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center; }
  .container__title {
    font-weight: 500;
    font-size: 24px;
    line-height: 29px;
    text-align: center; }
  .container__description {
    font-size: 16px;
    line-height: 24px;
    color: #2C2C2C; }
  .container__status {
    position: absolute;
    top: 54px;
    left: 31px;
    background: linear-gradient(261.65deg, #EFBB37 -3.95%, #CF9E1F 109.44%);
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
    border-radius: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px 20px; }
    .container__status span {
      font-weight: 500;
      font-size: 14px;
      line-height: 17px;
      color: white; }
  .container__header {
    display: flex;
    align-items: flex-start;
    justify-content: center; }
  .container__image {
    width: 256px;
    height: 261px;
    overflow: hidden;
    border-radius: 25px;
    position: relative; }
    .container__image:hover img {
      width: 140%;
      height: auto;
      max-width: 140%; }
    .container__image img {
      transition: 0.3s;
      top: 50%;
      left: 50%;
      height: auto;
      width: 100%;
      max-width: 100%;
      transform: translate(-50%, -50%);
      position: absolute; }
  .container__params form {
    width: 100%;
    display: flex;
    flex-wrap: wrap; }
    .container__params form .form__item {
      margin-bottom: 11px;
      margin-left: 22px; }
      .container__params form .form__item:last-child {
        margin-bottom: 26px; }
      .container__params form .form__item input {
        display: none; }
        .container__params form .form__item input:checked ~ label:after {
          content: "";
          left: 3px;
          top: 3px;
          width: 17px;
          height: 17px;
          position: absolute;
          background: linear-gradient(261.65deg, #EFBB37 -3.95%, #CF9E1F 109.44%);
          border-radius: 5px; }
      .container__params form .form__item label {
        position: relative;
        display: flex;
        align-items: center; }
        .container__params form .form__item label:before {
          content: "";
          display: block;
          width: 23px;
          height: 23px;
          background-color: #DEDEDE;
          border-radius: 5px;
          margin-right: 13px; }
  .container__atributs {
    width: 50%;
    display: flex;
    flex-direction: column; }
    .container__atributs .selectric-wrapper {
      margin-bottom: 16px; }
      .container__atributs .selectric-wrapper .selectric-items {
        border: none;
        box-shadow: none;
        background-color: white;
        border-radius: 0 0 15px 15px;
        overflow: hidden; }
        .container__atributs .selectric-wrapper .selectric-items .selectric-scroll ul li:hover {
          background-color: #F3F8FC; }
      .container__atributs .selectric-wrapper.selectric-open .selectric {
        border-radius: 15px 15px 0 0; }
      .container__atributs .selectric-wrapper .selectric {
        border: none;
        background-color: white;
        border-radius: 50px; }
        .container__atributs .selectric-wrapper .selectric .button {
          background-color: white; }
  .container__price {
    padding-top: 5px;
    width: 50%;
    text-align: right;
    font-weight: 600;
    font-size: 18px;
    line-height: 21px;
    color: black; }
  .container__bottom {
    width: 100%;
    display: flex; }
    .container__bottom input::-webkit-datetime-edit {
      padding: 1em; }
    .container__bottom input::-webkit-datetime-edit-fields-wrapper {
      background: silver; }
    .container__bottom input::-webkit-datetime-edit-text {
      color: red;
      padding: 0 0.3em; }
    .container__bottom input::-webkit-datetime-edit-month-field {
      color: blue; }
    .container__bottom input::-webkit-datetime-edit-day-field {
      color: green; }
    .container__bottom input::-webkit-datetime-edit-year-field {
      color: purple; }
    .container__bottom input::-webkit-inner-spin-button {
      display: none; }
    .container__bottom input::-webkit-calendar-picker-indicator {
      background: orange; }
  .container__counter {
    margin-left: 16px;
    display: flex;
    background: #E2E9F0;
    height: 39px;
    border-radius: 40px;
    padding: 0 13px;
    justify-content: space-between;
    min-width: 92px; }
    .container__counter input {
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: transparent;
      border: none;
      width: 30px;
      text-align: center;
      font-size: 16px; }
    .container__counter div {
      display: flex;
      align-items: center;
      cursor: pointer;
      -moz-user-select: none;
      -khtml-user-select: none;
      user-select: none; }
      .container__counter div.disable {
        pointer-events: none;
        opacity: 0; }
  .container__submit {
    flex: 1;
    margin-left: 31px;
    margin-right: 55px;
    cursor: pointer; }
    .container__submit button {
      position: relative;
      display: flex;
      height: 39px;
      background: linear-gradient(261.65deg, #EFBB37 -3.95%, #CF9E1F 109.44%);
      border-radius: 30px;
      border: none;
      width: 100%;
      align-items: center;
      justify-content: center;
      transition: 0.3s; }
      .container__submit button:hover {
        filter: drop-shadow(0px 0px 20px #E8CB80); }
      .container__submit button span {
        z-index: 3;
        font-weight: 600;
        font-size: 18px;
        line-height: 21px;
        color: white;
        text-transform: uppercase;
        position: relative;
        pointer-events: none; }

